.product-relate__content{position:relative}.product-relate__heading{padding:1rem;border:1px solid #e8e8e8;margin:6rem 0 2rem 0}.product-relate__heading h1{position:relative;padding:1rem 3rem;font-size:1.4rem;font-weight:500;background-color:#ffb900;border-color:#ffb900;display:inline-block;letter-spacing:.1rem;color:#fff;border-radius:.3rem}.product-relate__heading h1::after{position:absolute;content:"";border-width:.6rem .6rem;border-color:#ffb900 transparent transparent transparent;border-style:solid;top:100%;left:50%;transform:translateX(-50%)}.product-relate__slider{margin:0 -1rem}.product-relate .slick-slide{padding:0 1rem}.product-relate .slick-slider{position:unset}.product-relate .slick-arrow{top:3rem;outline:none}.product-relate .slick-arrow.slick-prev{left:calc(100% - 9.5rem)}.product-relate .slick-arrow.slick-next{right:1rem}.product-relate .slick-prev,.product-relate .slick-next{width:4rem;height:4rem;border:1px solid #ebebeb;color:#e8e8e8}.product-relate .slick-prev:hover,.product-relate .slick-next:hover{outline:none;border-color:#000}.product-relate .slick-prev::before{font-family:FontAwesome;content:"";font-size:1.4rem;color:#9b9797}.product-relate .slick-next::before{font-family:FontAwesome;content:"";font-size:1.4rem;color:#9b9797}@media(max-width: 414px){.product-relate__heading h1{font-size:1.2rem}}